
import Blur from 'vux/src/components/blur/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import ScoreItem from '@/components/ScoreItem'
import {mapGetters, mapMutations} from 'vuex'
import {wallet, message} from '../config'
export default {
  name: 'personer',
  head: {
    title: {
      inner: '个人中心'
    }
  },
  data () {
    return {
      message: [],
      logout: false,
      balance: 0,
      cumulative: 0,
      withdraw: 0,
      userId: 'null',
      id: ''
    }
  },
  computed: {
    ...mapGetters({
      userInfo: 'getUserInfo',
      checkAuthor: 'checkAuthor'
    })
  },
  created () {
    if (this.$localStorage.get('logined') === 'true') {
      this.userId = JSON.parse(this.$localStorage.get('userInfo')).userId
      this.$http({
        method: 'jsonp',
        url: wallet,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: {
          userId: this.userId
        }
      })
      .then(res => {
        this.$http({
          method: 'jsonp',
          url: message,
          jsonp: 'callback',
          jsonpCallback: 'json',
          params: {
            userId: JSON.parse(this.$localStorage.get('userInfo')).userId,
            limit: 10,
            pageIndex: 0
          }
        })
        .then(res => {
          res.body.data.messageList.forEach(el => {
            if (!el.status) {
              this.message.push(el)
            }
          })
        })
        if (res.body.status) {
          this.balance = res.body.data.wallet.balance
          this.cumulative = res.body.data.wallet.cumulative
          this.withdraw = res.body.data.wallet.withdraw
          this.$localStorage.set('balance', this.balance)
        } else {
          this.$vux.toast.show({
            type: 'text',
            width: '15em',
            position: 'bottom',
            text: '登录过期，请重新登录！',
            time: '1000'
          })
        }
      })
    }
  },
  components: {
    Blur,
    XImg,
    ScoreItem,
    Group,
    Cell,
    XButton,
    Confirm
  },
  methods: {
    checkAuthor () {
      if (this.userId === 'null') {
      }
    },
    selectBlur () {
      if (!this.userInfo) {
        return 'static/img/blur.jpg'
      } else {
        if (this.userInfo.userSex > 1) {
          return 'static/img/blur2.jpg'
        } else {
          return 'static/img/blur1.jpg'
        }
      }
    },
    handleLogout () {
      this.$vux.toast.show({
        type: 'text',
        width: '20em',
        text: '成功退出，即将跳到登录页！',
        time: '1000'
      })
      this.postLogout(this)
    },
    ...mapMutations({
      postLogout: 'postLogout'
    })
  }
}



